.circle {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  /* background-color: #ff4343; */
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.circle:before,
.circle:after {
  content: '';
  display: block;
  position: absolute;
  top: 0; 
  right: 0; 
  bottom: 0; 
  left: 0;
  border: 1px solid rgb(207,181,59);
  border-radius: 50%;
}


.circle:before {
  animation: ripple 4s linear infinite;
}

.circle:after {
  animation: ripple 4s linear 1s infinite;
}

@keyframes ripple{
  0% { transform: scale(1); }
  50% { transform: scale(1.3); opacity:1; }
  100% { transform: scale(1.6); opacity:0; }
}
